.section-4{
  background-image: url($src+'images/bg_section_2.jpg');
  &__slide{
    width: 35%;
    position: absolute;
    bottom: 15%;
    left: 7%;
    @include maxs($sp-1400) {
      width: 50%;
    }
    .title{
      font-size: rem(28);
    }
    p{
      font-size: rem(14);
      margin-top: rem(10);
    }
    .carousel-control-next{
      top: -25%;
      right: -22%;
    }
    .carousel-indicators{
      top: -15%;
      left: 0%;
      right: initial;
      margin-left: 0%;
    }
    .carousel-control-next-icon {
        background-image: url(data:image/svg+xml;utf8;base64,);
        width: rem(75);
        height: rem(75);
      }
  }
}

