        bottom: 0;
        width: 0%;
        height: 2px;
        background: #fff;
        transition: 500ms; }
      header ul.navbar-nav li a.nav-link:hover {
        text-decoration: none; }
        header ul.navbar-nav li a.nav-link:hover:after {
          position: absolute;
